
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import { goodsBucketDelete } from "@/servers/api";
import InLineXNumber from "@/components/common/InLineXNumber";

export default {
props: ["dataListCon", "countArr","checkFlagArr"],
components: {
GroupTitle,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XButton,
InLineXNumber,
XNumber,
CheckIcon
},
mounted() {},
methods: {
delGoods(id, index) {
goodsBucketDelete({
sid: id
})
.then(res => {
console.log(res);
if (res.result === 1) {
this.$vux.toast.show({
type: "success",
text: "删除成功",
time: 1000,
onHide: () => {
this.$emit("changeList", index);
}
});
}
})
.catch(err => {
console.log(err);
});
},
change(val) {
console.log("change", val);
},
onButtonClickCol(id, type) {

},
onButtonClickDel(id, index) {
this.delGoods(id, index);
},
handleEvents(type) {

}
},
data() {
return {
disabled: false,
changeValue: 1
};
},
updated() {}
};



