
import XButton from 'vux/src/components/x-button/index.vue'

import Header from "@/components/common/Header";
import SwiperOut from "@/components/common/SwiperOut";
import DividedArea from "@/components/common/DividedArea";
import Divider from "@/components/common/Divider";
import GoodsList from "@/components/layout/GoodsList";
import { ShopList, goodsBucketRecomm } from "@/servers/api";
import { parse } from "path";

export default {
name: "",
props: [""],
data() {
return {
desc: "精选好物",
TitleObjData: {
titleContent: "购物车",
showLeftBack: true,
showRightMore: false
},
numArr: [],
dataList: [],
checkFlagArr: [],
goodsListData:[],
marketTotalPrice: 0
};
},

components: {
Header,
SwiperOut,
DividedArea,
GoodsList,
Divider,
XButton
},

computed: {
orderListCon() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {
this.getDataList();
this.getGoodsComm();
},
updated() {
this.getMarketTotalPrice();
},
methods: {

getGoodsComm() {
goodsBucketRecomm({
type:1
})
.then(res => {
if(res.result === 1){
this.goodsListData = res.data.result;
}
})
.catch(err => {
console.log(err);
});
},


getMarketTotalPrice() {
let amount = 0;
for (let i = 0; i < this.checkFlagArr.length; i++) {
if (this.checkFlagArr[i]) {
amount += this.numArr[i] * parseInt(this.dataList[i].marketprice, 10);
}
}
this.marketTotalPrice = amount.toFixed(2);
},
updateList(index) {
this.dataList.splice(index, 1);
this.numArr.splice(index, 1);
},

getDataList() {
ShopList({
page: 1
})
.then(res => {
if (res.result === 1) {
let obj = res.data.result;
for (let i in obj) {
for (let j in obj[i][0]) {
this.dataList.push(obj[i][0][j]);
this.numArr.push(parseInt(obj[i][0][j].total, 10));
this.checkFlagArr.push(true);
}
}
}
})
.catch(err => {
console.log(err);
});
}
},

watch: {}
};



